@import "../../common.scss";

.sidebar-menu {
    list-style: none;
    margin: 0 0 40px 0;
    padding: 0;
    li {
        a {
            display: block;
            .title {
                font-size: 1.2rem;
            }
            .subtitle {
                font-size: .8rem;
            }
        }
    }

    .btn-lightbg {
        background-color: $lightBG;
        border-color: $lightBG;
        color: $bodyBackground;
        &:hover {
          background-color: $lightBGHover;
          border-color: $lightBGHover;
          color: $bodyBackground;
        }
        &:disabled {
          background-color: #222;
          border-color: #000;
          color: 0;
        }
      }
}